import React from "react"
import {Formik , Field, Form, ErrorMessage} from "formik"
import * as yup from 'yup'
import { boolean } from "yup/lib/locale";
import './signInForm.css'

function SignIn() {
		return(
		<div className = 'form'>

			<h1 className = 'title'>SIGN IN TO YOUR ACCOUNT</h1>
			
			<Formik

				initialValues = {{email: '', password:'', check: false}}
				validationSchema = {yup.object({

					email: yup.string()
						.email('invalid email address')
						.required('Required'),

					password: yup.string()
						.min(8, 'unsafe pass, write more then 8 symbols')
						.required('Required'),
					check: yup.boolean()
						.oneOf([true], 'you have accept it')
					
				})}

				onSubmit = {(values, {setSubmitting}) => {
					setTimeout(() => {
						alert('welcome');
						setSubmitting(false);
					}, 400);
				}}
				>

					<Form>
						
							<Field name = 'email' type = 'email'
							placeholder = 'johnnythedesigner@gmail.com'
							className = 'inp'
							/>
							<ErrorMessage className = 'err' name = 'email' component = 'div'/>
					
							<Field name = 'password' type = 'password' placeholder = '********'
							className = 'inp'
							/>
							<ErrorMessage className = 'err' name = 'password' component = 'div'/>

						<div className = 'check'>
							<Field name = 'check' type = 'checkbox'/>	
							<h1>Keep me signed in</h1>
						</div>
						<ErrorMessage className = 'err' name = 'check' component = 'div'/>


						<button type = 'submit' className = 'btn'><p>SIGN IN</p></button> 

					</Form>

				</Formik>
				<a href = '#'>Forgot your password?</a>
			</div>
		)
	
}

export default SignIn;